import React from "react";
import { ArrowRightOutlined } from "@ant-design/icons";

function About({ activeTest }) {
    return (
        <div className="header-about sizeblock">
            <div className="header-about__text">
                <div className="header-about__text-slogan"><span className="header-about__text-name"> <span className="header-about__text-y">Y</span>t<span className="header-about__text-y2">Y</span>t</span>  ,    ,
                       </div>
                <div className="header-about__text-test"> ,       </div>
                <div className="header-about__light-btn">
                    <div className="header-about__text-button button" onClick={() => { activeTest(true) }}> 
                        <span className="header-about__text-buttonarrow arrow"><ArrowRightOutlined /></span>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default About;